.pesontedan 
  position: relative
  margin-bottom: 1px
  width: 100%
  color: $color-text
  overflow: hidden
  margin: 0 auto
  
.pesontedan:hover
  color: $hover-color-text

.pesontedan input 
  position: absolute
  opacity: 0
  z-index: -1

.pesontedan .pesontedan__label 
  position: relative
  display: block
  padding: 0 0 0 1em
  background: $background-color-main
  line-height: 3
  cursor: pointer
  font-size: 17px
  border-bottom: 1px solid $border-color

.pesontedan>.pesontedan__label:hover
  box-shadow: 0 0 5px $box-shadow-color

.pesontedan .pesontedan__label:hover
  text-shadow: 0 0 4px $text-shadow-color

.pesontedan__content 
  display: flex
  flex-direction: column
  background: $background-color-light
  max-height: 0
  //min-height: 7em
  overflow: hidden
  transition: max-height .01s
  font-size: 14px
  color: $color-text

.pesontedan__content.filter
  flex-direction: row

.pesontedan__item
  width: 100%
  padding: 5px 0 15px 0
.pesontedan__item > .pesontedan__label 
  font-size: 14px
  border-bottom: 1px solid $border-color

.pesontedan__content p 
  margin: 1em

.pesontedan__content button
  padding: 7px
  border: none
  font: inherit
  color: inherit
  background-color: transparent
  text-align: left
.pesontedan__content button:hover
  text-shadow: 0 0 4px $bright-text-shadow-color

/* :checked */
.pesontedan input:checked ~ .pesontedan__content 
  max-height: 100vh
  padding: 10px 0 0 0

/* Icon */
.pesontedan .pesontedan__label::after 
  position: absolute
  right: 0
  top: 0
  display: block
  width: 3em
  height: 3em
  line-height: 3
  text-align: center
  -webkit-transition: all .40s
  -o-transition: all .40s
  transition: all .40s

.pesontedan input[type=checkbox] + .pesontedan__label::after 
  content: "\2B07"

.pesontedan input[type=radio] + .pesontedan__label::after 
  content: "\25BC"

.pesontedan input[type=checkbox]:checked + .pesontedan__label::after 
  transform: rotate(538deg)
  color: $color-text-active

.pesontedan input[type=radio]:checked + .pesontedan__label::after 
  transform: rotateX(180deg)
  color: $color-text-active

.pesontedan input[type=checkbox] + .label__item::after 
  content: "\2B07"
  

.pesontedan input[type=radio] + .label__item::after 
  content: "\25BC"

.pesontedan input[type=checkbox]:checked + .label__item::after 
  transform: rotate(180deg)
  color: $color-text-active

.pesontedan input[type=radio]:checked + .label__item::after 
  transform: rotateX(180deg)
  color: $color-text-active

.pesontedan__content button.ascending::after
  content: " \2191"

.pesontedan__content button.descending::after
  content: " \2193"


.pesontedan__content button.pesontedan__btnActive
  color: $color-text-active
label.pesontedan__label.pesontedan__sortActive
  border-bottom: 1px solid $border-color-main
label.pesontedan__label.pesontedan__sortActive:hover
  box-shadow: 0 0 5px $box-shadow-color-main
label.pesontedan__label.pesontedan__filterActive
  border-bottom: 1px solid $border-color-main
label.pesontedan__label.pesontedan__filterActive:hover
  box-shadow: 0 0 5px $box-shadow-color-main