@import "./color__palette/main-colors"
@import "./fonts/google-fonts"
//Стилизация header'а страницы


//---------------------------------------------------------
//уровень блока
.header
  display: flex
  flex-direction: row
  justify-content: flex-start
  align-items: center
  position: sticky
  top: 0
  border-bottom: 0.5px solid $border-color
  z-index: 9999
  text-align: justify
  letter-spacing: 1px
  height: 8vh
  padding: 0.3em 2%
  background:$background-color-main
  color: $hover-color-text
//уровень элементов
.header__nav
  display: flex
.header h1 
  margin: 0
  font-size: 1.5em
  height: 60%
  display: inline-block
  color: $hover-color-text
  padding: 5px 20px 0 20px
  border: 1px solid $border-color
  border-radius: 15px
  cursor: default
.header img
  width: 55px
  background: $white-color-element
  transition: 0.35s ease-in-out
.header img:hover
  background: $grey-color-element
.header img::before 
  content: ''
  display: inline-block
  vertical-align: middle
  height: 100%
.header__tab
  text-decoration: none
  color: $color-text
  padding: 0 30px 0 30px
  font-size: $font-size-medium
  font-weight: $font-weight-bolt
  cursor: pointer
.header__tab:hover
  color: $hover-color-text
  text-shadow: 0 0 5px $text-shadow-color
.header__tab.active
  color: $color-text-active