@import "../adaptive/mobile-devices__item-channels"
@import "../color__palette/main-colors"
@import "../fonts/google-fonts"
@import "../elements/pesontedan"
//Стилизация списка каналов/фильмов/сериалов (контента)


//---------------------------------------------------------
//Стилизация header'а списка для контента
.content__header 
  display: grid
  grid-gap: 1.3em
  grid-template-rows: auto
  gap: 10px
.content__header>span
  padding: 0px 0px 15px 0px
  margin: 25px
  font-size: $font-size-large
  color: $hover-color-text
  font-weight: $font-weight-bolt


//---------------------------------------------------------
//Стилизация непосредственного списка (контента)
.content__item-main
  margin: 25px
//уровень блока
.content__item-channels
  display: grid
.content__item-channels>div
  transition: 0.2s ease-in-out
  background: -webkit-linear-gradient(right, $main-color-element 0, $main-color-element 100%) bottom no-repeat
  background: -o-linear-gradient(right, $main-color-element 0, $main-color-element 100%) bottom no-repeat
  background: linear-gradient(to right, $main-color-element 0, $main-color-element 100%) bottom no-repeat
  background-size: 70% 0px
.content__item-channels>div:hover
  border-radius: 5px
  box-shadow: 0 5px 5px -5px $bright-color-element
  background-size: 100% 1.5px


//уровень одного элемента
//стилизация элементов плитки при рендере списка
a.channel
  @extend .content__header 
  min-width: 90px
  align-items: center
  text-decoration: none
  color: $color-text
a.channel:hover .channel__information>span:first-child
  color: $hover-color-text
  text-shadow: 0 0 4px $text-shadow-color
  box-shadow: 0 -6px 6px -6px $box-shadow-color
a.channel:hover .channel__information>span:nth-child(2n)
  color: $main-color-element
  text-shadow: 0 0 1px $text-shadow-color

a.channel>.channel__image>img
  width: 100%
  border-radius: 5px
.channel__image
  overflow: hidden
  position: relative
a.channel:hover .channel__image>img
  transform: scale(1.1)
  opacity: 0.6
  transition: 0.35s ease-in-out

.channel__information
  display: grid
  grid-template-rows: 55px
  visibility: visible
  padding-bottom: 15px
  overflow: hidden


//Стилизация счетчика страниц
.content__item-page
  padding: 25px 0 0 25px
  font-size: $font-size-medium
  color: $hover-color-text

//стилизация блоков фильтрации и сортировки
.content__actions
  display: flex
  flex-direction: column
  margin: 0 auto
  width: 100%
.content__actions>div
  display: flex
  justify-content: flex-start
  padding-top: 5px
.content__element
  float: left
  width: 100%
  padding: 0 1.5em
.content__actions-filter
  padding-bottom: 5px
.content__actions-sort
  padding-bottom: 15px
.content__actions-user-warning
  justify-content: flex-start
  padding: 0 0 0 25px
  color: $color-text-error