@import "./tabs__style/tvlist"
@import "header"
@import "./elements/prevNext__blocs/showMore"
@import "./tabs__style/aboutUs"
@import "./color__palette/main-colors"
@import "./fonts/google-fonts"


//---------------------------------------------------------
//Базовые стили для всей страницы
body
  margin: 0
  background-color: $background-color-main
  font-family: $font-family-Montserrat
  letter-spacing: $letter-spacing-standart
  font-size: $font-size-lighter

.basic-styles
  width: 100%
  height: 100%

.main__block
  display: flex
  flex-direction: column

.main__container
  display: flex
  flex-direction: column
  justify-content: center

.error__critical
  display: flex
  flex-direction: column
  color: $color-text
  width: 60vw
  margin: 10vh auto
.error__critical h1 
  color: $color-text-error
.error__image
  width: 100%
  opacity: 55%
  border-radius: 15px
.loading__message
  color: $color-text
.error__btn-refresh
  display: flex
  align-items: center
  justify-content: center
  background-color: inherit
  font-family: $font-family-Montserrat
  letter-spacing: $letter-spacing-standart
  font-size: $font-size-lighter
  border-radius: 5px
  margin-top: 2em
  cursor: pointer
  color: $hover-color-text
  border: 1px solid $border-color
  height: 50px
.error__btn-refresh:hover
  box-shadow: 0 0 6px $box-shadow-color
  text-shadow: 0 0 4px $text-shadow-color