@media screen and (min-width: 1200px) {
  .content__item-channels {
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 25px;
  }

  .main__component {
    margin: 3em;
  }

  .header__nav {
    padding-left: 60px;
  }
}
@media screen and (max-width: 1200px) {
  .content__item-channels {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 20px;
  }

  .main__component {
    margin: 1em;
  }

  .header__nav {
    padding-left: 20px;
  }
}
@media screen and (max-width: 960px) {
  .content__item-channels {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 25px;
  }

  .main__component {
    margin: 0.9em;
  }

  .header__nav {
    padding-left: 10px;
  }
}
@media screen and (max-width: 840px) {
  .content__item-channels {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 25px;
  }

  .main__component {
    margin: 0.5em;
  }

  .header__nav {
    padding-left: 5px;
  }
}
.pesontedan {
  position: relative;
  margin-bottom: 1px;
  width: 100%;
  color: #e8f1f2a6;
  overflow: hidden;
  margin: 0 auto;
}

.pesontedan:hover {
  color: #e8f1f2ff;
}

.pesontedan input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.pesontedan .pesontedan__label {
  position: relative;
  display: block;
  padding: 0 0 0 1em;
  background: #0e1119;
  line-height: 3;
  cursor: pointer;
  font-size: 17px;
  border-bottom: 1px solid #888888;
}

.pesontedan > .pesontedan__label:hover {
  box-shadow: 0 0 5px #d8d8d8;
}

.pesontedan .pesontedan__label:hover {
  text-shadow: 0 0 4px #c4c4c4d4;
}

.pesontedan__content {
  display: flex;
  flex-direction: column;
  background: #272f4560;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.01s;
  font-size: 14px;
  color: #e8f1f2a6;
}

.pesontedan__content.filter {
  flex-direction: row;
}

.pesontedan__item {
  width: 100%;
  padding: 5px 0 15px 0;
}

.pesontedan__item > .pesontedan__label {
  font-size: 14px;
  border-bottom: 1px solid #888888;
}

.pesontedan__content p {
  margin: 1em;
}

.pesontedan__content button {
  padding: 7px;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  text-align: left;
}

.pesontedan__content button:hover {
  text-shadow: 0 0 4px #fc4700;
}

/* :checked */
.pesontedan input:checked ~ .pesontedan__content {
  max-height: 100vh;
  padding: 10px 0 0 0;
}

/* Icon */
.pesontedan .pesontedan__label::after {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 3em;
  height: 3em;
  line-height: 3;
  text-align: center;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}

.pesontedan input[type=checkbox] + .pesontedan__label::after {
  content: "⬇";
}

.pesontedan input[type=radio] + .pesontedan__label::after {
  content: "▼";
}

.pesontedan input[type=checkbox]:checked + .pesontedan__label::after {
  transform: rotate(538deg);
  color: #db6a3de8;
}

.pesontedan input[type=radio]:checked + .pesontedan__label::after {
  transform: rotateX(180deg);
  color: #db6a3de8;
}

.pesontedan input[type=checkbox] + .label__item::after {
  content: "⬇";
}

.pesontedan input[type=radio] + .label__item::after {
  content: "▼";
}

.pesontedan input[type=checkbox]:checked + .label__item::after {
  transform: rotate(180deg);
  color: #db6a3de8;
}

.pesontedan input[type=radio]:checked + .label__item::after {
  transform: rotateX(180deg);
  color: #db6a3de8;
}

.pesontedan__content button.ascending::after {
  content: " ↑";
}

.pesontedan__content button.descending::after {
  content: " ↓";
}

.pesontedan__content button.pesontedan__btnActive {
  color: #db6a3de8;
}

label.pesontedan__label.pesontedan__sortActive {
  border-bottom: 1px solid #db6a3de8;
}

label.pesontedan__label.pesontedan__sortActive:hover {
  box-shadow: 0 0 5px #db6a3de8;
}

label.pesontedan__label.pesontedan__filterActive {
  border-bottom: 1px solid #db6a3de8;
}

label.pesontedan__label.pesontedan__filterActive:hover {
  box-shadow: 0 0 5px #db6a3de8;
}

.content__header, a.channel {
  display: grid;
  grid-gap: 1.3em;
  grid-template-rows: auto;
  gap: 10px;
}

.content__header > span, a.channel > span {
  padding: 0px 0px 15px 0px;
  margin: 25px;
  font-size: 2em;
  color: #e8f1f2ff;
  font-weight: bold;
}

.content__item-main {
  margin: 25px;
}

.content__item-channels {
  display: grid;
}

.content__item-channels > div {
  transition: 0.2s ease-in-out;
  background: -webkit-linear-gradient(right, #db6a3de8 0, #db6a3de8 100%) bottom no-repeat;
  background: -o-linear-gradient(right, #db6a3de8 0, #db6a3de8 100%) bottom no-repeat;
  background: linear-gradient(to right, #db6a3de8 0, #db6a3de8 100%) bottom no-repeat;
  background-size: 70% 0px;
}

.content__item-channels > div:hover {
  border-radius: 5px;
  box-shadow: 0 5px 5px -5px #fc4700;
  background-size: 100% 1.5px;
}

a.channel {
  min-width: 90px;
  align-items: center;
  text-decoration: none;
  color: #e8f1f2a6;
}

a.channel:hover .channel__information > span:first-child {
  color: #e8f1f2ff;
  text-shadow: 0 0 4px #c4c4c4d4;
  box-shadow: 0 -6px 6px -6px #d8d8d8;
}

a.channel:hover .channel__information > span:nth-child(2n) {
  color: #db6a3de8;
  text-shadow: 0 0 1px #c4c4c4d4;
}

a.channel > .channel__image > img {
  width: 100%;
  border-radius: 5px;
}

.channel__image {
  overflow: hidden;
  position: relative;
}

a.channel:hover .channel__image > img {
  transform: scale(1.1);
  opacity: 0.6;
  transition: 0.35s ease-in-out;
}

.channel__information {
  display: grid;
  grid-template-rows: 55px;
  visibility: visible;
  padding-bottom: 15px;
  overflow: hidden;
}

.content__item-page {
  padding: 25px 0 0 25px;
  font-size: 1.1em;
  color: #e8f1f2ff;
}

.content__actions {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
}

.content__actions > div {
  display: flex;
  justify-content: flex-start;
  padding-top: 5px;
}

.content__element {
  float: left;
  width: 100%;
  padding: 0 1.5em;
}

.content__actions-filter {
  padding-bottom: 5px;
}

.content__actions-sort {
  padding-bottom: 15px;
}

.content__actions-user-warning {
  justify-content: flex-start;
  padding: 0 0 0 25px;
  color: #ea4444;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: sticky;
  top: 0;
  border-bottom: 0.5px solid #888888;
  z-index: 9999;
  text-align: justify;
  letter-spacing: 1px;
  height: 8vh;
  padding: 0.3em 2%;
  background: #0e1119;
  color: #e8f1f2ff;
}

.header__nav {
  display: flex;
}

.header h1 {
  margin: 0;
  font-size: 1.5em;
  height: 60%;
  display: inline-block;
  color: #e8f1f2ff;
  padding: 5px 20px 0 20px;
  border: 1px solid #888888;
  border-radius: 15px;
  cursor: default;
}

.header img {
  width: 55px;
  background: #fff;
  transition: 0.35s ease-in-out;
}

.header img:hover {
  background: #909090;
}

.header img::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.header__tab {
  text-decoration: none;
  color: #e8f1f2a6;
  padding: 0 30px 0 30px;
  font-size: 1.1em;
  font-weight: bold;
  cursor: pointer;
}

.header__tab:hover {
  color: #e8f1f2ff;
  text-shadow: 0 0 5px #c4c4c4d4;
}

.header__tab.active {
  color: #db6a3de8;
}

.main__showMore {
  margin: 20px;
}

.showMore__block {
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 80vw;
  margin: 0 auto;
}

.showMore__block.active {
  color: #e8f1f2ff;
  border: 1px solid #e3e2e2;
}

.showMore__block.disable {
  color: #e8f1f2a6;
  border: 1px solid #888888;
}

.showMore__block.active:hover {
  box-shadow: 0 0 6px #d8d8d8;
  text-shadow: 0 0 4px #c4c4c4d4;
}

.aboutUs__container {
  display: flex;
  height: 35vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.aboutUs__list {
  display: flex;
  height: 35vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  list-style-type: none;
  margin: 0;
  padding: 50px 0 0 0;
  overflow: hidden;
  font-size: 1.2em;
  font-weight: bold;
  letter-spacing: 0.1px;
  color: #e8f1f2ff;
  font-family: "Montserrat", sans-serif;
}

.aboutUs__list > li > a {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  padding: 6px 0px;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 0.1px;
  color: #e8f1f2a6;
  transition: 0.4s ease-in-out;
  background: -webkit-linear-gradient(right, #db6a3de8 0, #db6a3de8 100%) bottom no-repeat;
  background: -o-linear-gradient(right, #db6a3de8 0, #db6a3de8 100%) bottom no-repeat;
  background: linear-gradient(to right, #db6a3de8 0, #db6a3de8 100%) bottom no-repeat;
  background-size: 70% 1px;
}

.aboutUs__list > li > a:hover {
  color: #e8f1f2ff;
  border-radius: 5px;
  text-shadow: 0 0 5px #c4c4c4d4;
  box-shadow: 0 5px 5px -5px #fc4700;
  background-size: 100% 1.5px;
}

body {
  margin: 0;
  background-color: #0e1119;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.1px;
  font-size: 0.97em;
}

.basic-styles {
  width: 100%;
  height: 100%;
}

.main__block {
  display: flex;
  flex-direction: column;
}

.main__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.error__critical {
  display: flex;
  flex-direction: column;
  color: #e8f1f2a6;
  width: 60vw;
  margin: 10vh auto;
}

.error__critical h1 {
  color: #ea4444;
}

.error__image {
  width: 100%;
  opacity: 55%;
  border-radius: 15px;
}

.loading__message {
  color: #e8f1f2a6;
}

.error__btn-refresh {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: inherit;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.1px;
  font-size: 0.97em;
  border-radius: 5px;
  margin-top: 2em;
  cursor: pointer;
  color: #e8f1f2ff;
  border: 1px solid #888888;
  height: 50px;
}

.error__btn-refresh:hover {
  box-shadow: 0 0 6px #d8d8d8;
  text-shadow: 0 0 4px #c4c4c4d4;
}

