$media: screen
$layout-breakpoint-high: 1200px
$layout-breakpoint-medium: 960px
$layout-breakpoint-small: 840px

@media #{$media} and (min-width: $layout-breakpoint-high)
  .content__item-channels
      grid-template-columns: repeat(7, 1fr)
      grid-gap: 25px
  .main__component
    margin: 3em
  .header__nav
    padding-left: 60px
@media #{$media} and (max-width: $layout-breakpoint-high)
  .content__item-channels
    grid-template-columns: repeat(5, 1fr)
    grid-gap: 20px
  .main__component
    margin: 1em
  .header__nav
    padding-left: 20px
@media #{$media} and (max-width: $layout-breakpoint-medium)
  .content__item-channels
      grid-template-columns: repeat(5, 1fr)
      grid-gap: 25px
  .main__component
    margin: 0.9em
  .header__nav
    padding-left: 10px
@media #{$media} and (max-width: $layout-breakpoint-small)
  .content__item-channels
      grid-template-columns: repeat(3, 1fr)
      grid-gap: 25px
  .main__component
    margin: 0.5em
  .header__nav
    padding-left: 5px