@import "../color__palette/main-colors"
@import "../fonts/google-fonts"
//Стилизация блока/таба AboutUs


//---------------------------------------------------------
//уровень блока
.aboutUs__container
  display: flex
  height: 35vh
  flex-direction: column
  justify-content: space-between
  align-items: center
.aboutUs__list
  display: flex
  height: 35vh
  flex-direction: column
  justify-content: space-between
  align-items: center
  text-decoration: none
  list-style-type: none
  margin: 0
  padding: 50px 0 0 0
  overflow: hidden
  font-size: 1.2em
  font-weight: $font-weight-bolt
  letter-spacing: $letter-spacing-standart
  color: $hover-color-text
  font-family: $font-family-Montserrat

//стилизация ссылок
.aboutUs__list>li>a
  display: inline-block
  text-decoration: none
  text-transform: uppercase
  padding: 6px 0px
  font-size: $font-size-medium
  font-weight: $font-weight-bolt
  letter-spacing: $letter-spacing-standart
  color: $color-text
  transition: 0.4s ease-in-out
  background: -webkit-linear-gradient(right, $main-color-element 0, $main-color-element 100%) bottom no-repeat
  background: -o-linear-gradient(right, $main-color-element 0, $main-color-element 100%) bottom no-repeat
  background: linear-gradient(to right, $main-color-element 0, $main-color-element 100%) bottom no-repeat
  background-size: 70% 1px
.aboutUs__list>li>a:hover 
  color: $hover-color-text
  border-radius: 5px
  text-shadow: 0 0 5px $text-shadow-color
  box-shadow: 0 5px 5px -5px $bright-color-element
  background-size: 100% 1.5px