@import "../../color__palette/main-colors"
//Стилизация элементов для реализации пагинации (блок showMore)


//---------------------------------------------------------
//общие стили для дочерних элементов showMore
@mixin showMore__block-element
  display: flex
  align-items: center
  justify-content: center
  height: 50px
  width: 80vw
  margin: 0 auto
//общие стили для блока
.main__showMore
  margin: 20px
//стили для элемента
.showMore__block
  border-radius: 5px
  cursor: pointer
  @include showMore__block-element
.showMore__block.active
  color: $hover-color-text
  border: 1px solid $border-color-hover
.showMore__block.disable
  color: $color-text
  border: 1px solid $border-color
.showMore__block.active:hover
  box-shadow: 0 0 6px $box-shadow-color
  text-shadow: 0 0 4px $text-shadow-color